import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { useStaticQuery, graphql } from "gatsby"
import ParallaxBanner from "../components/parallaxBanner"
import { ParallaxProvider } from "react-scroll-parallax"

const TrapprenoveringPage = () => {
    const { gcms } = useStaticQuery(graphql`
    {
        gcms {
            trapprenovering(where: {id: "ckfu2pfj40p7n01062nnkdfvh"}) {
                hero {
                  url
                }
                subImage1 {
                  url
                }
                subImage2 {
                  url
                }
              }
        }
    }
  `)
    return (
        <Layout>
            <SEO title="Trapprenovering" />
            <ParallaxProvider>
                <div class="cntnr column-flex">
                <ParallaxBanner url={gcms.trapprenovering.hero.url} topMargin="-80px" amount="0.2" heading="Trapprenovering"/>
                    <section class="content-section">
                     <h2 class="section-header">En vacker trappa förhöjer känslan i hela rummet. Vi ser till att din trappa är i bästa skick genom: </h2>
                       <br/> <h3>
                            Slipning
                    </h3>
                        <p>
                            Med en trappslipning blir trappan som ny igen. Vi slipar alla typer av trätrappor.
                    </p>
                        <h3>
                            Renovering
                    </h3>
                        <p>
                            Vi kan lägga alla typer av golv i din trappa. Allt från massiva trägolv till  tillskuren matta.
                            Trätrappor är levande och varje trappa har sin egna struktur och uttryck.
                            Att lägga matta i trappan bidrar till en tystare och hemtrevligare miljö.
                            Mattan kan läggas som löpande trappmatta eller appliceras i bitar på enskilt plansteg.
                    </p>
                        <p>
                            Vi på Victoria Golv löser den mest avancerade trapprenoveringen. Vi är proffs på att ge trappor ett nytt liv!
                    </p>
                    </section>
                    <section class="stairs-section">
                        <div class="stairs-img-container" >
                            <img src={gcms.trapprenovering.subImage1.url} alt="bild på en trappa tagen nerifrån"/>
                        </div>
                        <div class="stairs-img-container">
                            <img src={gcms.trapprenovering.subImage2.url} alt="bild på en trappa tagen uppifrån"/>
                        </div>
                    </section>
                </div>
            </ParallaxProvider>
        </Layout>
    )
}

export default TrapprenoveringPage;